.lds-dual-ring {
	display: block;
	width: 80px;
	height: 80px;
	margin: 0 auto;
}
.lds-dual-ring:after {
	content: ' ';
	display: block;
	width: 64px;
	height: 64px;
	margin: 8px;
	border-radius: 50%;
	border: 6px solid #cef;
	border-color: #cef transparent #cef transparent;
	animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
	0% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
	}
}
